import { Container, Row, Col, Tab, Nav } from "react-bootstrap";
import Aos from "aos";
import "aos/dist/aos.css";
import { useEffect } from "react";
import corporativo from "../Archivos/Imagenes/corporativo.jpg"
import eventos from "../Archivos/Imagenes/eventos.jpeg"
import estudio from "../Archivos/Imagenes/estudio.jpg"
import outdoor from "../Archivos/Imagenes/outdoor.jpg"
import portfoliomoda from "../Archivos/Imagenes/portfoliomoda.jpg"
import viajes from "../Archivos/Imagenes/viajes.jpeg"


export const Projects = () => {
  useEffect(() => {
    Aos.init({duration: 2000});   
   }, []);

  return (
 
    <Container id="trabajos" data-aos="fade-up" className="projects">
       
       <Row className="projectrow"> 
       
       <Col className="projectcol" size={12} sm={6} md={4}>
      <div className='proj-imgbx'>
        <img src={corporativo} />
        <div className='proj-txtx'>
          <h4>corporativo</h4>
          <span>Fotos para empresas</span>
          
        </div>
      </div>
    </Col>
    <Col className="projectcol" size={12} sm={6} md={4}>
      <div className='proj-imgbx'>
        <img src={eventos} />
        <div className='proj-txtx'>
          <h4>Eventos</h4>
          <span>Fotos para eventos</span>
          
        </div>
      </div>
    </Col>
    <Col className="projectcol" size={12} sm={6} md={4}>
      <div className='proj-imgbx'>
        <img src={estudio} />
        <div className='proj-txtx'>
          <h4>Fotos de estudio</h4>
          <span>Fotos en estudios de fotos</span>
          
        </div>
      </div>
    </Col>
    </Row>

    <Row className="projectrow"> 
    <Col className="projectcol" size={12} sm={6} md={4}>
      <div className='proj-imgbx'>
        <img src={outdoor} />
        <div className='proj-txtx'>
          <h4>Outdoor</h4>
          <span>Fotos al aire libre</span>
          
        </div>
      </div>
    </Col>
    <Col className="projectcol" size={12} sm={6} md={4}>
      <div className='proj-imgbx'>
        <img src={portfoliomoda} />
        <div className='proj-txtx'>
          <h4>Portfolio de moda</h4>
          <span>Fotos para completar tu portfolio de moda</span>
          
        </div>
      </div>
    </Col>
    <Col className="projectcol" size={12} sm={6} md={4}>
      <div className='proj-imgbx'>
        <img src={viajes} />
        <div className='proj-txtx'>
          <h4>Viajes</h4>
          <span>Fotos para conmemorar tus viajes</span>
          
        </div>
      </div>
    </Col>
        </Row>
        
        
      </Container>
     
 
   
  );
};